@tailwind base;
@tailwind components;
@tailwind utilities;

body {
    background-color: #131313;
}

.f-v {
    @apply flex flex-col;
}

.f-h {
    @apply flex flex-row;
}

.form-label {
    @apply mb-1 text-sm text-zinc-500;
}

.form-input {
    @apply p-2 rounded-md border-0 text-sm bg-neutral-900 text-white;
}

.form-input:disabled {
    @apply text-zinc-500;
}

.form-select {
    @apply form-input;
    border-right: 4px solid transparent;
}